import * as React from 'react'

function App() {
    const [count, setCount] = React.useState(0)
    return (
        <html>
            <head>
                <title>myRemix</title>
            </head>
            <body>
                <p>myRemix@sodeog.ai</p>
                <button onClick={() => setCount(c => c + 1)}>{count}</button>
            </body>
        </html>
    )
}

export default App